<template>
  <h1>Register</h1>
  <div>
    <p class="have_account_paragraph">Already have account? <router-link to="/login" class="login">Login</router-link></p>
    <p v-if="error">Stupid nigga!</p>
    <form @submit.prevent="Register()">
      <p>
        <input type="email" placeholder="email" class="email" id="email" v-model="login" required>
      </p>
      <p>
        <input type="text" placeholder="Password" class="password" id="password" v-model="password" pattern="({8,16}" required>
      </p>
      <p>
        <input type="submit" value="Sign Up" class="register" >
      </p>
    </form>
    <router-link to="/" class="home">Home</router-link>
  </div>
</template>

<script>
import axios from 'axios'
export default {

  data () {
    return {
      login: '',
      password: '',
      error: ''
    }
  },
  methods: {
    Register () {
      const user = JSON.stringify({ login: this.login, password: this.password })
      const customConfig = {
        headers: {
          'Content-Type': 'application/json'
        }
      }
      axios.post('api/account/signup', user, customConfig).then(function (response) {
        this.$router.push('/login')
        console.log(response)
      }).catch(function (error) {
        console.log(error)
      })
      // {headers:{"Content-Type" : "application/json"}}
      // const result = axios.post('api/account/signup', user, customConfig)
      // .then(() => { this.$router.push('/login') }, error => { console.log(error) })
      // this.$store.dispatch('api/account/signup', { login: this.login, password: this.password })
    }
  }
}
</script>

<style scoped>
.have_account_paragraph {
  font-size: 20px;
  margin-left: 0px;
  color: gray;
}
.register {
  margin: 20px 0 0 0px;
  width: 95px;
  font-size: 25px;
  height: 42px;
  padding: 5px;
  background-color: orange;
  border-radius: 20px;
  color: white;
  border: none;
  text-decoration: none;
}

.register:hover {
  opacity: 0.5;
}

.register:active {
  transform: translateY(4px);
}
.login {
  color: blue;
  text-decoration: none;
}
.email {
  height: 40px;
  width: 400px;
  margin: 10px 0px 0px 0px;
  border: 1px solid lightblue;
  outline: none;
  font-size: 20px;
}
.password {
  height: 40px;
  width: 400px;
  margin: 10px 0px 0px 0px;
  border: 1px solid lightblue;
  outline: none;
  font-size: 20px;
}
.home{
  font-size: 20px;
  margin-top: 40px;
  color: blue;
  text-decoration: none;
}

</style>
